@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,500;1,700;1,900&display=swap);
.container-header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 80px;
    background-color: cadetblue;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
    color: #f1f1f1;
    z-index: 3;
}

/* counter cart  start */
#cart-counter {
    position: absolute;
    top: -22px;
    right: -22px;
    width: 15px;
    height: 15px;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background: yellow;
    color: black;
}
/* counter cart end */


.container-header div {
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    box-sizing: border-box;
}

/* header left */
.container-header div:nth-child(1) {
    width: 10%;
    font-size: 28px;
    cursor: pointer;
}

/* header middle */
.container-header div:nth-child(2) {
    width: 80%;
}

.container-header div:nth-child(2) input {
    width: 40%;
    font-size: 20px;
    padding: 8px;
    border-radius: 5px;
    border-style: solid;
}

/* header right */
.container-header div:nth-child(3) {
    width: 10%;
}

.container-header div:nth-child(3) span {
    margin: 10px;
    cursor: pointer;
    transition: .5s;
    position: relative;
}

.container-header div:nth-child(3) span:nth-child(1):hover {
    color: red;
}

.container-header div:nth-child(3) span:nth-child(2):hover {
    color: yellow;
}



/* header responsive */
@media screen and (max-width: 600px) {
.container-header div {
    margin-top: 5px;
}

  /* header left */
.container-header div:nth-child(1) {
    width: 20%;
    font-size: 20px;
}

/* header middle */
.container-header div:nth-child(2) {
    width: 80%;
}

.container-header div:nth-child(2) input {
    width: 80%;
    font-size: 15px;
    padding: 5px;
}

/* header right */
.container-header div:nth-child(3) {
    width: 100%;
    justify-content: space-around;
}

.container-header div:nth-child(3) span {
    margin: 10px;
    cursor: pointer;
}  
}
.item-cart {
    width: 100%;
    height: 60px;
    box-sizing: border-box;
    transition: .5s;
    border: 2px solid #fff;
    border-width: 1px 0px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 15px 10px;
}

.item-cart:hover {
    border-color: #4b4b4b;
    box-shadow: 0px 0px 3px #4b4b4b;
}

.item-cart .item-info {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    font-size: 15px;
}

.item-cart .item-info:nth-child(1) {
    width: 15%;
    background-color: #b4b4b4;
    color: #f1f1f1;
} 

.item-cart .item-info:nth-child(2) {
    padding-left: 5px;
    justify-content: left;
    width: 45%;
}

.item-cart .item-info:nth-child(3) {
    width: 10%;
}

.item-cart .item-info:nth-child(4) {
    width: 20%;
}

.item-cart .item-info:nth-child(5) {
    width: 10%;
    cursor: pointer;
    transition: .5s;
}
.item-cart .item-info:nth-child(5):hover {
    color: red;
}
.container-paycheck {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 120px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
}

.container-paycheck span {
    width: 50%;
    height: 50%;
    box-sizing: border-box;
    padding: 5px;
    font-size: 20px;

}
.container-paycheck span:nth-child(2) {
    text-align: right;
    font-size: 28px;
    font-weight: 500;
}

.container-paycheck button {
    width: 95%;
    height: 40%;
    font-size: 20px;
    color: #f1f1f1;
    background-color: blueviolet;
    border-style: none;
    border-radius: 5px;
    cursor: pointer;
}

@media screen and (max-width: 600px) {
    .checkout-paycheck {
        position: fixed;
        left: 0;
        bottom: 0;
        background-color: #fff;
        border-radius: 5px 5px 0px 0px;
        box-shadow: 0px -3px 3px #4b4b4b;
    }
}
.container-aside {
    width: 30%;
    height: calc(100vh - 80px);
    position: fixed;
    right: -30%;
    top: 80px;
    border-left: 1px solid #b4b4b4;
    background-color: #fff;
    z-index: 2;
    transition: .5s;
}

.container-aside-active {
    right: 0;
    top: 80px;
}

.container-aside section {
    width: 100%;
}

.container-aside section:nth-child(1) {
    height: 10%;
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    padding: 15px 10px;
    font-size: 20px;
}
.container-aside section:nth-child(1) span {
    font-weight: 400;
}
.container-aside section:nth-child(1) p {
    cursor: pointer;
    color: blueviolet;
    text-decoration: underline;
}

/* lista de filmes aqui */

.container-aside section:nth-child(2) {
    max-height: calc(100% - 30%);
    overflow-y: auto;
    padding-right: 10px;
}


/* finalizar compra */


/* responsive view */
@media screen and (max-width: 600px) {
    .container-aside {
        width: 100%;
        height: calc(80vh - 80px);
        position: fixed;
        right: 0;
        top: calc(-80vh - 80px);
        border-left: unset;
        box-shadow: 0px 2px 3px #b4b4b4;
        border-radius: 0px 0px 10px 10px;
        background-color: #fff;
        z-index: 2;
        transition: .5s;
    }
    
    .container-aside-active {
        right: 0;
        top: 80px;
    }

    /* lista responsiva */
    .container-aside section:nth-child(2) {
        max-height: calc(100% - 40%);
        padding-right: 0px;
    }
}
.container-movie {
    position: relative;
    width: 240px;
    height: 340px;
    border: 1px solid #b1b1b1;
    margin-bottom: 15px;
    transition: .5s;
}
.container-movie:hover {
    box-shadow: 0px 0px 3px black;
}

.container-movie .favorite {
    position: absolute;
    top: 0;
    right: 0;
    margin: 10px;
    color: #f1f1f1;
    cursor: pointer;
    transition: .5s;
}

.container-movie .favorite:hover {
    color: red;
}

.container-movie .container-image {
    display: block;
    width: 100%;
    height: 60%;
    background-color: #d4d4d4;
}

.container-movie p {
    width: 100%;
    height: 10%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.container-movie p span {
    display: flex;
    align-items: center;
}

.container-movie button {
    width: 100%;
    height: 10%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bolder;
    color: #f1f1f1;
    background-color: blueviolet;
    cursor: pointer;
    outline: none;
    border: none;
    border-radius: 3px 3px 0px 0px;
}

/* responsive container */
@media screen and (max-width: 600px) {
    .container-movie {
        width: 45%;
        height: 280px;
        margin-bottom: 15px;
    }

    .container-movie p {
        font-size: 12px;
    }
}
.main-home {
    width: 100%;
    min-height: 100vh;
    margin-top: calc(80px + 10px);
    padding: 15px 8%;
    box-sizing: border-box;
    display: flex;
    align-items: flex-start;
    justify-content: space-around;
    flex-wrap: wrap;
}
.main-checkout {
    width: 100%;
    min-height: calc(100vh - 90%);
    margin-top: calc(80px + 10px);
    padding: 15px 8%;
    box-sizing: border-box;
}

.main-checkout h1 {
    margin-bottom: 30px;
}

.main-checkout form {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-wrap: wrap;
}

.main-checkout form section {
    width: 48%;
    height: 400px;
}

/* formulario esquerda */
.main-checkout form section:first-child {
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
}

.main-checkout form section:first-child div {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.main-checkout form .input-full {
    box-sizing: border-box;
    width: 100%;
    height: 40px;
    border-radius: 5px;
    border-style: solid;
    border-color: #b4b4b4;
    padding-left: 5px;
    font-size: 20px;
}
.main-checkout form .input-half {
    box-sizing: border-box;
    /* width definido no proprio componente */
    height: 40px;
    border-radius: 5px;
    border-style: solid;
    border-color: #b4b4b4;
    padding-left: 5px;
    font-size: 20px;
}

/* formulario direita */
.main-checkout form section:last-child {
    position: relative;
    margin-bottom: 100px;
}

.main-checkout form section:last-child .label {
    display: flex;
    align-items: center;
    justify-content: left;
    height: 40px;
    font-size: 20px;
    font-weight: 400;
    padding: 10px 10px;
}
.main-checkout form section:last-child .label span{
    padding-left: 5px;
}
.main-checkout form section:last-child .label span:nth-child(1) {
    width: 15%;
    padding-left: 0px;
}
.main-checkout form section:last-child .label span:nth-child(2) {
    width: 45%;
}
.main-checkout form section:last-child .label span:nth-child(3) {
    width: 10%;
}
.main-checkout form section:last-child .label span:nth-child(4) {
    width: 20%;
}

.main-checkout .checkout-list {
    height: 160px;
    overflow-y: auto;
}


/* responsive view */
@media screen and (max-width: 600px) {
    .main-checkout form section {
        width: 100%;
        height: 300px;
    }

    .main-checkout form section:last-child .label {
        font-size: 14px;
    }
    .main-checkout form section:last-child .label span:nth-child(2) {
        padding-left: 10px;
    }
}
#modal {
    position: fixed;
    width: 40%;
    height: 200px;
    top: 30%;
    left: 30%;
    padding: 5px;
    background: #fff;
    border: 3px solid #b4b4b4;
    z-index: 3;
}

.modal-label {
    text-align: center;
    margin-top: 30px;
    color: #4b4b4b;
}

#modal-button {
    display: block;
    width: 80%;
    margin: 30px auto 30px auto;
    font-size: 20px;
    padding: 10px;
    font-weight: bolder;
    color: #f1f1f1;
    background: blueviolet;
    border-radius: 5px;
    border-style: none;
    cursor: pointer;
}
* {
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
}
